@import '../color.scss';

.container {
  color: red;
  h4 {
    color: $mainColor;
    padding-top: 0;
  }
  p {
    color: $secondColor;
  }
}
@mixin simpleDIv {
  background-color: rgb(255, 190, 164);
  color: snow;
  max-width: 500px;
  padding: 20px;
  border-radius: 5px;
  margin: 15px 0;
}

.my-alert {
  @include simpleDIv();
}
.alertGreen {
  @extend .my-alert;
  background-color: rgb(74, 175, 175);
}
