* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: black;
}
.container {
  margin: 20px;
  padding: 0;
}
.jumbo {
  margin: 20px;
  padding: 80px;
  background-image: url('./img/1.jpeg');
  background-size: cover;
  background-color: hsl(152, 11%, 87%);
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  border-radius: 10px;
}
.jumbo h1 {
  font-weight: 600;
}
.jumbo p {
  text-align: start;
  font-weight: 400;
  font-size: 1.2em;
}

.contents {
  display: grid;
  padding: 20px;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
}
.content:hover {
  transform: translate(0, -2%);
  box-shadow: rgb(117, 117, 117, 0.3) 0px 2px 4px;
}
.content {
  font-size: 0.8rem;
  text-align: center;
  transition: 250ms ease-in-out;
  border-radius: 10px;
}
.content h4 {
  margin-top: 0.4em;
  font-size: 1rem;
}
.content p {
  margin-bottom: 0.4em;
}
.imgContainer {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}
.imgContainer img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
